import { By } from 'selenium-webdriver';
import { DriverLike, register } from '@oracle/oraclejet-webdriver';
import { ListItemLayoutWebElement } from './ListItemLayoutWebElement';
export { ListItemLayoutWebElement };

/**
 * Retrieve an instance of [[ListItemLayoutWebElement]].
 * @example
 * ```javascript
 * import { findListItemLayout } from '@oracle/oraclejet-core-pack/webdriver';
 * const el = await findListItemLayout(driver, By.id('my-oj-c-list-item-layout'));
 * ```
 * @param driver A WebDriver/WebElement instance from where the element will be
 * searched. If WebDriver is passed, the element will be searched globally in the
 * document. If WebElement is passed, the search will be relative to this element.
 * @param by The locator with which to find the element
 */
export async function findListItemLayout(
  driver: DriverLike,
  by: By
): Promise<ListItemLayoutWebElement> {
  const webEl = await driver.findElement(by);
  // Check that the element is of type ListItemLayoutWebElement
  if (!(webEl instanceof ListItemLayoutWebElement)) {
    const tagName = await webEl.getTagName();
    throw Error(
      `findListItemLayout(${by}) created ${webEl.constructor.name} for <${tagName}>, but expected ${ListItemLayoutWebElement.name}`
    );
  }
  return webEl;
}

register('oj-c-list-item-layout', ListItemLayoutWebElement);
